@import "./../../Utils/colors";

// RE-USABLE CODE
@mixin navTextStyles($font-weight: 600, $font-size: 1em, $padding: 10px) {
  padding: $padding;
  color: $white;
  font-weight: $font-weight;
  font-size: $font-size;
  cursor: pointer;
  transition: all 200ms ease-in;

  &:hover,
  &.visited {
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.search {
  height: 50px;
}
.input {
  height: 100%;
  width: 100%;
  border: none;
}

.wrapper {
  width: 100%;
  height: 100%;
  background-color: $dark-blue;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.navigationSection {
  .categorySection {
    @include navTextStyles;
  }

  .iconStyles {
    color: #f1c40f;
    margin-right: 10px;
    transform-origin: center;
    transition: all 200ms ease-out;
  }

  .iconRotated {
    transform: rotateZ(90deg);
  }

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;

    li {
      @include navTextStyles(300, 0.85em, 10px 20px);
      span:first-child {
        font-size: inherit;
      }
      display: flex;
      justify-content: space-between;
      align-items: center;
      .innerIconStyles {
        margin-right: 10px;
      }
    }
  }
}

.footer {
  padding: 10px;
  border: 2px $dark-grey dashed;
  border-radius: 5px;
  margin: 1rem 1.5rem;
  font-size: 0.7em;
  color: $white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  a {
    font-size: 1.2em;
    display: block;
    padding: 2px;
    text-decoration: none;
    background-color: $medium-grey;
    border-radius: 3px;
    cursor: pointer;
    margin-top: 5px;
    color: black;

    svg {
      color: $header-blue;
    }
  }
}
